import React, { Fragment} from "react";
import TotalR from "./TotalR";

const Total = (props) => {
  return (
    <Fragment>
      <div className="Total">
        <span>Total pour une personne</span>
        <span>{props.NbrTotal}</span>
      </div>
      <TotalR total={props.NbrTotal}/>
    </Fragment>
  );
};

export default Total;
