import React, { Fragment, useState } from "react";

const ActiviteOne = (props) => {
  return (
    <div
      key={props.item.id}
      className={props.item.select === true ? "Check" : "noCheck"}
      onClick={() => props.HandClick(props.item)}
    >
      <span className="name">{props.item.name}</span>
      <span className="prix">{props.item.prix}</span>
    </div>
  );
};

export default ActiviteOne;
