import React, { useState } from "react";
import ActiviteOne from "./ActiviteOne";

const Activites =(props)=>{
    
    const HandClick = (obj)=>{
        props.HandChange({...obj})
    }

    return(
        
        <div className="Activites">
      {
        props.data.map(ele=><ActiviteOne item={ele} HandClick={HandClick}/>)
      }
      </div>
        
    )
}

export default Activites