import React, { useEffect, useState } from "react";
import { nanoid } from "nanoid";
import Activites from "./component/Activites/Activites";
import Total from "./component/Total/Total";
import Notification from "./component/Notification/Notification";
import "./App.css";
const App = () => {
  //data
  const [data, setData] = useState([
    {
      id: 1,
      name: "Monter à dos d'éléphant",
      prix: 200,
      select: true,
    },
    {
      id: 2,
      name: "Défoncer le Machu Picchu",
      prix: 400,
      select: false,
    },
    {
      id: 3,
      name: "Visiter les tombeaux égyptiens",
      prix: 650,
      select: false,
    },
    {
      id: 4,
      name: "Prendre des selfies avec un tigre",
      prix: 300,
      select: false,
    },
  ]);

  //change true or false
  const HandChange = (obj) => {
    data.map((ele) =>
      ele.id === obj.id && obj.select === true
        ? (ele.select = false)
        : ele.id === obj.id && obj.select === false
        ? (ele.select = true)
        : null
    );
    setData([...data]);
  };

  console.log(data);
  const [NbrActvites, setNbrActvites] = useState(0); 
  const [NbrTotal, setNbrTotal] = useState(0);
  
  //Notification
  useEffect(
    () =>
      setNbrActvites(data.reduce((Nbr, obj) => Nbr + (obj.select ? 1 : 0), 0)),
    [data]
  );
 
  //Total
  useEffect(
    () =>
      setNbrTotal(data.reduce((Nbr, obj) => Nbr + (obj.select ? parseInt(obj.prix): 0), 0)),
    [data]
  );




  return (
    <div className="main">
      <div className="header">
        <h2>Activites Touristique</h2>
      </div>
      <Notification noti={NbrActvites} />
      <Activites data={data} HandChange={HandChange} />
      <Total NbrTotal={NbrTotal} />
    </div>
  );
};

export default App;
